header {
  text-align: center;
  h1 {
    color: $color-1;
    margin: 10px;
    font-weight: 600;
  }
  input {
    border: 1px solid #80808088;
    border-radius: 10px;
    padding: 10px;
    width: 250px;
  }
}
.recipes {
  ul {
    padding: 20px;
    display: grid;
    overflow: hidden;
    grid-template-columns: repeat(auto-fill, 300px);
    grid-column-gap: 5px;
    grid-row-gap: 5px;
    justify-content: center;
  }
}
