.card {
  width: 300px;
  height: 100%;
  border: 1px solid $color-1;
  text-align: center;
  padding: 10px;
  position: relative;
  margin: 10px;
  border-radius: 10px;
  h2 {
    font-weight: 600;
    margin: 10px;
  }
  .origin {
    font-weight: 100;
  }
  img {
    width: 100%;
    margin: 10px auto;
  }
  pre {
    font-weight: 100;
    text-align: justify;
    font-size: 1rem;
    white-space: pre-wrap;
    font-family: $font-1;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
  }
}
