$font-1: sans-serif;
$color-1: salmon;
$color-2: rgb(62, 60, 60);
$white: rgb(242, 236, 236);

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: $white;
  font-family: $font-1;
}

li {
  list-style-type: none;
}
